import React,{useState,useEffect,useRef} from 'react';
import './App.css';




const App = () => {

const[name,setName]=useState("");





const handleChange =(event) =>{
setName(event.target.value);
}

const[clicked,setClicked]=useState(false);

const HandleClick=()=>{
  setClicked (!clicked);

    if (name===""){
      alert("Please enter input")
    }
    
    
    
}




  return (
    <div className="container">
     {clicked ? <div className="content">
     <input type="text" 
     autoFocus className="focus"
     name="name"
     value={name} 
     onChange={handleChange}
     placeholder="please enter your food"
      />

     <button className="btn1"
     onClick={HandleClick} >Close</button>
     </div>: null} 
     <div className="main">
     <button className="btn2" onClick={() => setClicked (!clicked)}>Click-Me</button>
     </div>
    </div>
  )
}



export default App;
